<template>
  <vs-row class="movie-page">
    <vs-col vs-type="flex" vs-w="6" style="padding-bottom:1em;padding-left:2em">
      <vs-button
        color="primary"
        type="border"
        v-if=" page != 1 && page != 0  "
        @click="$emit('prev');"
      >Page {{page }}</vs-button>
    </vs-col>
    <vs-col
      vs-type="flex"
      vs-w="6"
      vs-justify="flex-end"
      vs-align="flex-end"
      style="padding-bottom:1em;padding-right:2em"
    >
      <vs-button
        color="primary"
        type="border"
        v-if=" page != 0 && page != totalPage"
        @click="$emit('more')"
      >Page {{page + 1}}</vs-button>
    </vs-col>
  </vs-row>
</template>

<script>
export default {
  props: {
    page: {
      type: Number,
      required: true
    },
    totalPage: {
      type: Number,
      required: true
    },
    more: {
      type: Function
    }
  }
};
</script>
